import tuiIcon from "@/components/icon/icon"
export default {
	components: {
		tuiIcon
	},
	data() {
		return {
			ordersn: '', //订单编号
			id: '', //商品ID
			goods: {},

		};
	},
	onLoad(option) {
		this.id = option.id;
		this.ordersn = option.ordersn;
		this.getDetail();
	},
	methods: {
		getDetail() {
			let data={
				order_sn:this.ordersn,
				proid:this.id
			}
			this.$xm.post('/Service/proinfo',data,(res)=>{
				this.goods=res.data;
				this.goods.pro_sn='http://img.xmvogue.com/thumb/' + this.goods.pro_sn + '.jpg?x-oss-process=style/300';
				uni.setStorageSync('refund',res.data)
			})
		},
		togoods(e) {
			uni.navigateTo({
				url:'../../../goods/goods?gid='+e
			})
		},
		toType(e){
			if(e==1){
				uni.navigateTo({
					url:'money/money'
				})
			}else if(e==2){
				uni.navigateTo({
					url:'refundGood/refundGood'
				})
			}else if(e==3){
				uni.navigateTo({
					url:'barter/barter'
				})
			}
		}
	}
}
